@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
html,body,#root{height:100%;margin:0px;padding:0px}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{height:100%}

.text-grid-area::-webkit-scrollbar{width:8px;height:8px}.text-grid-area::-webkit-scrollbar-track{background-color:#1e1e1e}.text-grid-area::-webkit-scrollbar-thumb{background:rgba(255,255,255,0.1);border-radius:10px}.text-grid-area::-webkit-scrollbar-thumb:hover{background:rgba(255,255,255,0.2)}.text-grid-area:focus{outline:none}.text-grid-area{width:100%;height:100%;background-color:#1e1e1e;position:relative;overflow:auto;display:flex;flex-wrap:nowrap;flex:1 1}.text-grid-status-bar{box-sizing:content-box;display:flex;background-color:#383838;color:white;border:1px solid #1e1e1e;width:100%;height:24px;line-height:24px;font-size:smaller;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.text-grid-status-bar ul{display:block;padding:0px;margin:0px}.text-grid-status-bar ul li{display:inline-block;margin-left:10px}.text-grid{position:-webkit-sticky;position:sticky;display:block;box-sizing:content-box;left:0px;top:0px;border:1px solid #252526;color:#dcdcaa;background-color:#1e1e1e;width:100%;height:100%;overflow:hidden;line-height:0px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:smaller;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.text-grid :focus{outline:none}.text-grid .text-grid-selection{box-sizing:border-box;border:1px solid rgba(220,220,170,0.5);background-color:rgba(255,255,255,0.1);position:absolute;display:inline-block;z-index:1}.text-grid .text-grid-row{box-sizing:border-box;display:block;overflow:hidden;width:100%;margin-top:-1px}.text-grid .text-grid-row .text-grid-cell{position:relative;box-sizing:border-box;border:1px solid #252526;display:inline-block;justify-content:center;flex-direction:column;height:100%;margin-left:-1px;overflow:hidden;text-align:center;vertical-align:middle}.text-grid .text-grid-row .text-grid-cell .text-grid-cell-content{display:flex;flex-direction:column;width:100%;height:100%;justify-content:center}.text-grid .text-grid-row .row-hover{background-color:#252526}.text-grid .text-grid-row .col-hover{background-color:#252526}.text-grid .text-grid-row .cell-hover{background-color:#383838}.text-grid .text-grid-row .active-override{background-color:#383838;color:#9cdcfe;z-index:1}.text-grid .text-grid-row .active-override-top{border-top:1px solid #9cdcfe}.text-grid .text-grid-row .active-override-left{border-left:1px solid #9cdcfe}.text-grid .text-grid-row .active-override-right{border-right:1px solid #9cdcfe}.text-grid .text-grid-row .active-override-bottom{border-bottom:1px solid #9cdcfe}.text-grid .text-grid-row .active-override:after{content:'';position:absolute;width:0;height:0;border:solid 4px transparent}.text-grid .text-grid-row .active-override-top:after{top:0%;left:50%;margin-left:-4px;border-top:solid 4px #9cdcfe}.text-grid .text-grid-row .active-override-bottom:after{bottom:0%;left:50%;margin-left:-4px;border-bottom:solid 4px #9cdcfe}.text-grid .text-grid-row .active-override-left:after{top:50%;left:0px;margin-top:-4px;border-left:solid 4px #9cdcfe}.text-grid .text-grid-row .active-override-right:after{top:50%;right:0%;margin-top:-4px;border-right:solid 4px #9cdcfe}.text-grid .text-grid-row .active-insert{border-left:1px solid #9cdcfe}

.terminal{padding-top:10px;padding-right:0px;padding-bottom:0px;border-top:1px solid #252526;background:#171717;display:flex;flex-direction:col}.terminal textarea::-webkit-scrollbar{width:8px;height:8px}.terminal textarea::-webkit-scrollbar-track{background-color:#1e1e1e}.terminal textarea::-webkit-scrollbar-thumb{background:rgba(255,255,255,0.1);border-radius:10px}.terminal textarea::-webkit-scrollbar-thumb:hover{background:rgba(255,255,255,0.2)}.terminal textarea{outline:none;resize:none;overflow:auto;display:flex;flex:1 1;width:100%;border:0px solid #888;background:#171717;color:#dcdcaa;padding:10px;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.toolbar{box-sizing:content-box;display:flex;background-color:#383838;color:white;border:1px solid #1e1e1e;width:100%;height:24px;line-height:24px;font-size:smaller;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.toolbar ul{display:block;padding:0px;margin:0px}.toolbar ul li{display:inline-block;margin-left:10px;padding:0 10px}.toolbar ul .button:hover{background:#252526;cursor:pointer;color:#9cdcfe}

.befungeIde{display:flex;flex-direction:row;height:100%}.befungeIde .main{display:flex;flex:1 1;flex-direction:column}.befungeIde .asside{display:flex;flex-direction:column;width:300px;background-color:#252526;font-size:smaller}.befungeIde .asside .asside-header{font-weight:bold;color:white;padding:3px;padding-left:10px;background-color:#383838;border-top:1px solid #171717;border-bottom:1px solid #171717}.befungeIde .asside .asside-section{font-weight:bold;padding:10px;font-weight:normal;color:white;background-color:#252526}.befungeIde .befunge-stack-view{margin:0px;padding:0px}.befungeIde .befunge-stack-view li{list-style:none;display:inline-block;width:100%;padding:5px;border-bottom:1px solid #171717}.befungeIde .befunge-stack-view li:hover{background-color:#171717}

